//eslint-disable//
import React, { Component, Fragment } from "react";
import {
  Button,
  Form,
  FormControl,
  InputGroup,
  Card
} from "react-bootstrap";
import { Formik, FieldArray } from "formik";
import * as yup from "yup";
import Select from 'react-select';
import { getBrokerByID, addUnloading, updateBroker, getAllOrderParty, getAllPendingSalesOrder } from "./PurchaseOrderService";
import { withRouter } from "react-router-dom";
import DatePicker from "react-datepicker";
import { GenesGroup } from "../../../AllOptions"
import { isMobile } from "@utils"
import moment from "moment"
import { useRef } from 'react';
class LoadingEditor extends Component {
  state = {
    
    item: this.props.item,
    isLoadingParty: false,
    PartyOptions: [],
    PartyValue: null,
    quantityInvalid: false,
    loading: false,
    date: new Date(),
    pendingSalesOrder: [],
    details: {
      loading_obj: this.props.item?.id,
      pendingSalesOrder: [],
      unload_to: "",
      date: new Date().toISOString().slice(0, 10),
      frieght: "",
      quantity: "",
      advance_frieght: "",
      remarks: "",
    },
    id: "",
  };
  async componentDidMount() {
    console.log(this.props.id)
    this.setState({ isLoadingBroker: true, isLoadingParty: true })
    if (this.props.id!=="create") {
      getBrokerByID(this.props.match.params.id)
        .then((res) => {
          this.setState({ details: res.data });
          this.setState({ PartyValue: { label: res.data.loading_from_name, value: res.data.loading_from_type === "sales" || res.data.loading_from_type === "mandi-sales" ? "party=" + res.data.unload_to : "broker=" + res.data.unload_to } })
        }
        )
        .catch((error) => {
          console.log(error)
        })
    }
    else {
      this.setState({ id: 0 });
    }
    const res = await getAllOrderParty() 
    this.setState({isLoadingParty: false})
    this.setState({ PartyOptions: this.assembleOption(res.data) })
  }

  assembleOption = (list) => {
    let broker = list.filter(party => { return party.party_type === "broker" })
    let partys = list.filter(party => { return party.party_type === "sales" })
    let mandi = list.filter(party => { return party.party_type === "mandi-sales" })
    let broker_options = broker.map(company=>{
      return {
        label : company.name,
        value: "broker="+ company.id,
        val: company.id
      }
    })
    let party_options = partys.map(company=>{
      return {
        label : company.name,
        value: "party="+ company.id,
        val: company.id
      }
    })
    let mandi_options = mandi.map(company=>{
      return {
        label : company.name,
        value: "party="+ company.id,
        val: company.id
      }
    })
    return [{label: "Mandi", options:mandi_options},{label:"Party",options:party_options},{label:"broker",options:broker_options}]
  }
  ass = (list) => {
    let orders = list.map(order => {
      let ind = this.state.details.pendingSalesOrder.findIndex(x => x.id === order.id)
      if (ind === -1) {
        if (order.quantity_loaded !== undefined) {
          return {
            ...order
          }
        } else {
          return {
            ...order,
            quantity_loaded: ""
          }
        }
      } else {
        return {
          ...this.state.details.pendingSalesOrder[ind]
        }
      }
    })
    return orders
  }
  componentDidUpdate(prevProps, prevState) {
    if (prevState.PartyValue !== this.state.PartyValue) {
      if (this.state.PartyValue?.value !== undefined) {
        getAllPendingSalesOrder(this.state.PartyValue?.value, this.state.item?.genes)
          .then((res) => {
            this.setState({ details: { ...this.state.details, pendingSalesOrder: this.ass(res.data) } })
          })
          .catch((err) => {
            this.setState({ details: { ...this.state.details, pendingSalesOrder: [] } })
          })
      }
      else {
        this.setState({ details: { ...this.state.details, pendingSalesOrder: [] } })
      }
      console.log(this.state)
    }
  }

  handleSubmit = (values, { setSubmitting }) => {
    let { id } = this.state;
    this.setState({ loading: true });
    setSubmitting(true);
    if (this.props.id==="create") {
      addUnloading(values).then((res) => {
        this.setState({ loading: false });
        this.props.history.push(`/orders/loading/${res.data.id}`);
        this.props.toggleSalesOrderEditor();
      })
    } else {
      updateBroker(this.props.match.params.id, values).then(() => {
        this.setState({ loading: false });
        this.props.toggleSalesOrderEditor();
      });
    }
  };


  render() {
    const { isLoadingParty, loading, PartyOptions, PartyValue, date } = this.state;
    return (
      <div className="invoice-viewer py-3">
        <Formik initialValues={this.state.details} validationSchema={loadingSchema} onSubmit={this.handleSubmit}
          enableReinitialize={true}>
          {({
            values,
            errors,
            touched,
            handleChange,
            handleBlur,
            handleSubmit,
            setFieldValue,
            setFieldTouched

          }) => {
            return (
              <Fragment>
                <Form onSubmit={handleSubmit} className="px-4">
                  <div className="row mb-3 justify-content-between">
                    <div className="col-lg-12 text-end">
                    <div className="mb-4">
                          <Button type="button" className="me-3 py-2" variant="warning" onClick={() =>this.props.onClick(this.state.item.id)}
                            tabIndex={12}
                          >
                            Cancel
                          </Button>
                          <Button type="submit" className="py-2" variant="primary" disabled={loading} tabIndex={11}>
                            Save
                          </Button>
                        </div>
                        </div>
                  </div>
                  
                  <div className="form-group row">
                    <label className="ul-form__label ul-form--margin col-lg-3 col-form-label fw-bold">
                      Unloaded To :-
                    </label>
                    <div className="col-lg-2">
                      <Select
                        isClearable
                        isDisabled={isLoadingParty}
                        isLoading={isLoadingParty}
                        onChange={(value) => { setFieldValue("unload_to", value ? value.val : null); this.setState({ details: { ...this.state.details, unload_to: value ? value.val : null } }); this.setState({ PartyValue: value }) }}
                        options={PartyOptions}
                        menuPortalTarget={document.body}
                        value={PartyValue}
                        tabIndex={3}
                        onBlur={() => setFieldTouched("unload_to", true)}
                        isInvalid={
                          errors.unload_to &&
                          touched.unload_to}
                      />

                    </div>
                    <label className="ul-form__label ul-form--margin col-lg-3 col-form-label fw-bold">
                      Date :
                    </label>
                    <div className="col-lg-2">
                      <DatePicker
                        tabIndex={2}
                        className="form-control"
                        dateFormat="dd-MM-yyyy"
                        selected={new Date(date)}
                        onChange={(date) => {
                          if (date !== null) {
                            setFieldValue("date", date.toISOString().slice(0, 10))
                            this.setState({ date: date })
                          }
                        }
                        }
                        onBlur={() => setFieldTouched("date", true)}
                        isInvalid={
                          errors.date &&
                          touched.date}
                      />
                    </div>
                  </div>
                  <div className="form-group row">
                    <label className="ul-form__label ul-form--margin col-lg-3 col-form-label fw-bold">
                      Quantity :
                    </label>
                    <div className="col-lg-2">
                      <div className="input-group">
                        <FormControl value={values.quantity} name="quantity" type="text" inputMode='numeric'
                          placeholder="QTY" onChange={handleChange} onBlur={handleBlur} isInvalid={errors.quantity &&
                            touched.quantity} tabIndex={6} />
                        <div className="input-group-append">
                          <span className="input-group-text" id="basic-addon2" style={{ height:"34px", borderTopLeftRadius: "0rem", borderBottomLeftRadius: "0rem" }}>
                            Quintal
                          </span>
                        </div>
                      </div>
                    </div>
                    <label className="ul-form__label ul-form--margin col-lg-3 col-form-label fw-bold">
                      Frieght Per Quintal :
                    </label>
                    <div className="col-lg-2">
                      <FormControl value={values.frieght} name="frieght" type="text" placeholder="Frieght Per QTL"
                        onChange={handleChange} onBlur={handleBlur} isInvalid={errors.frieght && touched.frieght} />

                    </div>
                  </div>
                  <div className="form-group row">
                    <label className="ul-form__label ul-form--margin col-lg-3 col-form-label fw-bold">
                      Frieght by Party :
                    </label>
                    <div className="col-lg-2">
                      <FormControl value={values.advance_frieght} name="advance_frieght" type="text" placeholder="Frieght By Party"
                        onChange={handleChange} onBlur={handleBlur} isInvalid={errors.advance_frieght && touched.advance_frieght} />
                    </div>
                    <label className="ul-form__label ul-form--margin col-lg-3 col-form-label fw-bold">
                      Remarks :
                    </label>
                    <div className="col-lg-2">
                      <FormControl value={values.remarks} name="remarks" type="text" placeholder="Any Comment"
                        onChange={handleChange} onBlur={handleBlur} isInvalid={errors.remarks && touched.remarks} />
                    </div>
                  </div>
                  <div className="custom-separator"></div>
                  <FieldArray name="pendingSalesOrder">

                    {isMobile() ?
                      <>
                        {values.pendingSalesOrder?.map((item, index) => {
                          return (
                            <div className="mt-2 mb-2" key={item.id}>
                              <Card
                                elevation={0}
                                style={{backgroundColor:"#ADD8E6"}}
                                key={item.id}

                              >
                                <div className="row mt-2 ms-2 me-2 mb-2">
                                  <table className="table table-borderless" style={{ marginBottom: "0rem" }}>
                                    <tbody>
                                      <tr>
                                        <th>Date</th>
                                        <td>{moment(new Date(item.date)).format("DD-MMM-YYYY")}</td>
                                      </tr>
                                      <tr>
                                        <th>Total Quantity</th>
                                        <td className="text-capitalize">{item.quantity ? item.quantity : 0} {item.unit}</td>
                                      </tr>
                                      <tr>
                                        <th>Rate</th>
                                        <td className="text-capitalize">{item.rate ? item.rate : 0} Per QTL</td>
                                      </tr>
                                      <tr>
                                        <th>Pending Quanity</th>
                                        <td className="text-capitalize">{item.pending ? item.pending : 0} {item.unit}</td>
                                      </tr>
                                      <tr>
                                        <th>Quanity Loaded</th>
                                        <td className="text-capitalize"><div className="input-group">
                                          <input type="text" className="form-control" inputMode='numeric' style={{ borderTopRightRadius: "0.25rem", borderBottomRightRadius: "0.25rem", borderColor: "#003473" }}>
                                          </input>
                                          <div className="input-group-append">
                                            <span className="input-group-text" id="basic-addon2" style={{ backgroundColor: "#ADD8E6", borderRadius: "0", borderColor: "#ADD8E6" }}>
                                              {item.unit}
                                            </span>
                                          </div>
                                        </div></td>
                                      </tr>
                                    </tbody>
                                  </table>
                                </div>

                              </Card>
                              <div style={{ borderBottom: "1px dashed #003473", margin: "15px 0" }}></div>
                            </div>
                          )
                        })}
                      </> :

                      <div className="table-responsive">
                        <table className="table table-bordered" style={{ width: "100%", tableLayout: "fixed" }}>
                          {values.pendingSalesOrder?.length === 0
                            ? <></> :
                            <thead className="bg-gray-300">
                              <tr>
                                <th>Date</th>
                                <th scope="col">Item Name</th>
                                <th scope="col">Total Quantity</th>
                                <th scope="col">Rate</th>
                                <th scope="col">Pending Quantity</th>
                                <th scope="col">Quantity Loaded</th>
                              </tr>
                            </thead>}
                          <tbody>
                            {values.pendingSalesOrder?.filter(order => { if (values.genes) { return order.genes === values.genes } else return true }).map((item, index) => {
                              return (
                                <tr key={index}>
                                  <td>{moment(new Date(item.date)).format("DD-MMMM-YYYY")}</td>
                                  <td className="text-uppercase">{item.genes}</td>
                                  <td className="text-capitalize">{item.quantity} {item.unit}</td>
                                  <td className="text-capitalize">{item.rate}</td>
                                  <td className="text-capitalize">{item.pending ? item.pending - values.pendingSalesOrder[index].quantity_loaded : 0} {item.unit}</td>
                                  <td className="text-capitalize">
                                    <div className="input-group">
                                      <FormControl data-key={item.id} value={values.pendingSalesOrder[index].quantity_loaded} name={`pendingSalesOrder[${index}].quantity_loaded`} type="text" placeholder="Quantity"
                                        onChange={handleChange} onBlur={handleBlur} isInvalid={errors.pendingSalesOrder && touched.pendingSalesOrder && errors.pendingSalesOrder[index] && touched.pendingSalesOrder[index] && errors.pendingSalesOrder[index].quantity_loaded && touched.pendingSalesOrder[index].quantity_loaded} />
                                      <div className="input-group-append" >
                                        <span className="input-group-text" style={{ height:"34px", borderTopLeftRadius: "0rem", borderBottomLeftRadius: "0rem" }}>
                                          {item.unit}
                                        </span>
                                      </div>
                                    </div>
                                  </td>
                                </tr>
                              );
                            })}
                          </tbody>
                        </table>
                      </div>}
                  </FieldArray>
                </Form>
              </Fragment>
            );
          }}
        </Formik>
      </div>
    );
  }
}
const loadingSchema = yup.object().shape({
  loading_obj: yup.number().required(),
  unload_to: yup.string().required(),
  date: yup.string().required(),
  quantity: yup.string().required(),
  frieght: yup.number().optional(),
  advance_frieght: yup.number().optional(),
  remarks: yup.string().optional(),
  pendingSalesOrder: yup.array().of(yup.object().shape({
    broker: yup.string().optional().nullable(),
    broker_name: yup.string().optional().nullable(),
    condition: yup.string().optional().nullable(),
    date: yup.string().optional(),
    genes: yup.string().optional(),
    id: yup.string().optional(),
    party: yup.string().optional(),
    party_name: yup.string().optional(),
    pending: yup.number().optional(),
    purchase_type: yup.string().optional(),
    quantity: yup.number().optional(),
    rate: yup.number().optional(),
    remarks: yup.string().optional().nullable(),
    unit: yup.string().optional(),
    quantity_loaded: yup.number().optional().nullable().test("max", "Total should not exceed 100 %", function (value) {
      const { pending } = this.parent;
      if (value === undefined) {
        return true
      } else {
        return value <= pending;
      }
    })
  })),
});

export default withRouter(LoadingEditor);