import axios from '../../../../../axios/axios';

export const getAllOrderParty = async () => {
    return await axios.get(`/api/orders/party-list/all?party_type__in=mandi-sales&party_type__in=sales&party_type__in=broker`)
}
export const getAllPendingPurchaseOrder = async (party, genes) => {
    return await axios.get(`/api/orders/purchase-list/all?${party}&pending__gt=0`)

}

export const getAllPendingSalesOrder = async (party, genes) => {
    if (genes !== undefined)
        return await axios.get(`/api/orders/sales-list/all?${party}&pending__gt=0&genes=${genes}`)
    else
        return await axios.get(`/api/orders/sales-list/all?${party}&pending__gt=0`)

}

export const getAllPendingUnloading = async () => {
    return await axios.get('/api/orders/loading-list/all?unloaded=False')
}
export const getAllBroker = () => {
    return axios.get("/api/orders/broker-list/all")
}
export const getBrokerByID = (id) => {
    return axios.get("/api/orders/unloading/" + id)
}
export const deleteBroker = (id) => {
    return axios.delete("/api/orders/broker/" + id)
}
export const addUnloading = (broker) => {
    return axios.put("/api/orders/unloading/0", broker)
}
export const updateBroker = (id, company) => {
    return axios.patch("/api/orders/loading/" + id, company)
}