//eslint-disable//
import React, { Component, Fragment } from "react";
import {
  Button,
  Form,
  FormControl,
  InputGroup,
  Card
} from "react-bootstrap";
import { Formik, FieldArray } from "formik";
import * as yup from "yup";
import Select from 'react-select';
import { getBrokerByID, addBroker, updateBroker, getAllOrderParty, getAllPendingPurchaseOrder } from "./PurchaseOrderService";
import { withRouter } from "react-router-dom";
import DatePicker from "react-datepicker";
import { GenesGroup } from "../../../AllOptions"
import { isMobile } from "@utils"
import moment from "moment"

class LoadingEditor extends Component {
  state = {
    isLoadingParty: false,
    PartyOptions: [],
    PartyValue: null,
    quantityInvalid: false,
    loading: false,
    date: new Date(),
    pendingPurchaseOrder: [],
    details: {
      pendingPurchaseOrder: [],
      loadingOrders: [],
      loading_from: "",
      date: new Date().toISOString().slice(0, 10),
      genes: "",
      quantity: "",
      vehicle_number: "",
      remarks: "",
    },
    id: "",
  };
  componentDidMount() {
    this.setState({ isLoadingBroker: true, isLoadingParty: true })
    if (!this.props.isNewSalesOrder) {
      getBrokerByID(this.props.match.params.id)
        .then((res) => {
          this.setState({ details: res.data });
          this.setState({ PartyValue: { label: res.data.loading_from_name, value: res.data.loading_from_type === "purchase" || res.data.loading_from_type === "mandi-purchase" ? "party=" + res.data.loading_from : "broker=" + res.data.loading_from } })
        }
        )
        .catch((error) => {
          console.log(error)
        })
    }
    else {
      this.setState({ id: 0 });
    }
    getAllOrderParty("mandi-purchase").then((res) => {
      this.setState({ isLoadingParty: false })
      this.setState({ PartyOptions: [{ label: "Mandi", options: this.assembleOption(res.data, "party=") }] })
    })
    getAllOrderParty("purchase").then((res) => {
      this.setState({ isLoadingParty: false })
      this.setState({ PartyOptions: [...this.state.PartyOptions, { label: "Party", options: this.assembleOption(res.data, "party=") }] })
    })
    getAllOrderParty("broker").then((res) => {
      this.setState({ isLoadingParty: false })
      this.setState({ PartyOptions: [...this.state.PartyOptions, { label: "broker", options: this.assembleOption(res.data, "broker=") }] })
    })
  }

  assembleOption = (list, str) => {
    let companies = list.map(company => {
      return {
        label: company.name,
        value: str + company.id,
        val: company.id,
      }
    })
    return companies
  }
  assembleLoadingOrder = (list) => {
    let orders = list.map(company => {
      return {
        purchase_order: company.id,
        quantity_loaded: ""
      }
    })
    return orders
  }

  handlequantity = (value) => {
    let purchase_order_id = value.target.getAttribute('data-key')
    let index = this.state.details.loadingOrders.findIndex(x => x.purchase_order === purchase_order_id)
    let x = this.state.details.pendingPurchaseOrder[this.state.details.pendingPurchaseOrder.findIndex(x => x.id == purchase_order_id)]
    if (x.pending < value.target.value) {
      this.setState({ quantityInvalid: true })
    } else {
      this.setState({ quantityInvalid: false })
      if (index === -1) {
        let newVal = { purchase_order: purchase_order_id, quantity_loaded: value.target.value }
        this.setState({ details: { ...this.state.details, loadingOrders: [...this.state.details.loadingOrders, newVal] } })
      } else {
        let loading = this.state.details.loadingOrders
        loading[index].quantity_loaded = value.target.value
        this.setState({ details: { ...this.state.details, loadingOrders: loading } })
      }
    }

  }
  ass = (list) => {
    let orders = list.map(order => {
      let ind = this.state.details.pendingPurchaseOrder.findIndex(x => x.id === order.id)
      if (ind === -1) {
        if (order.quantity_loaded !== undefined) {
          return {
            ...order
          }
        } else {
          return {
            ...order,
            quantity_loaded: ""
          }
        }
      } else {
        return {
          ...this.state.details.pendingPurchaseOrder[ind]
        }
      }
    })
    return orders
  }
  componentDidUpdate(prevProps, prevState) {
    if (prevState.PartyValue !== this.state.PartyValue) {
      if (this.state.PartyValue?.value !== undefined) {
        getAllPendingPurchaseOrder(this.state.PartyValue?.value)
          .then((res) => {
            this.setState({ details: { ...this.state.details, pendingPurchaseOrder: this.ass(res.data) } })
          })
          .catch((err) => {
            this.setState({ details: { ...this.state.details, pendingPurchaseOrder: [] } })
          })
      }
      else {
        this.setState({ details: { ...this.state.details, pendingPurchaseOrder: [] } })
      }
    }
  }

  handleSubmit = (values, { setSubmitting }) => {
    let { id } = this.state;
    this.setState({ loading: true });
    setSubmitting(true);
    if (this.props.isNewSalesOrder) {
      addBroker(values).then((res) => {
        this.setState({ loading: false });
        this.props.history.push(`/orders/loading/${res.data.id}`);
        this.props.toggleSalesOrderEditor();
      })
    } else {
      updateBroker(this.props.match.params.id, values).then(() => {
        this.setState({ loading: false });
        this.props.toggleSalesOrderEditor();
      });
    }
  };


  render() {
    const { isLoadingParty, loading, PartyOptions, PartyValue, date } = this.state;
    return (
      <div className="invoice-viewer py-3">
        <Formik initialValues={this.state.details} validationSchema={loadingSchema} onSubmit={this.handleSubmit}
          enableReinitialize={true}>
          {({
            values,
            errors,
            touched,
            handleChange,
            handleBlur,
            handleSubmit,
            setFieldValue,
            setFieldTouched

          }) => {
            return (
              <Fragment>
                <Form onSubmit={handleSubmit} className="px-4">
                  <div className="row mb-3 justify-content-between">
                    <div className="col-md-3">
                      <h4 className="fw-bold">Loading</h4>
                    </div>
                    <div className="col-md-2 text-right">

                      <div className="justify-content-end">
                        <div className="mb-4">
                          <Button type="button" className="me-3 py-2" variant="warning" onClick={() =>
                            this.props.history.push('/orders/sales/list')}
                            tabIndex={12}
                          >
                            Cancel
                          </Button>
                          <Button type="submit" className="py-2" variant="primary" disabled={loading} tabIndex={11}>
                            Save
                          </Button>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="custom-separator"></div>
                  <div className="form-group row">
                    <label className="ul-form__label ul-form--margin col-lg-3 col-form-label fw-bold">
                      Date :
                    </label>
                    <div className="col-lg-2">
                      <DatePicker
                        tabIndex={2}
                        className="form-control"
                        dateFormat="dd-MM-yyyy"
                        selected={new Date(date)}
                        onChange={(date) => {
                          if (date !== null) {
                            setFieldValue("date", date.toISOString().slice(0, 10))
                            this.setState({ date: date })
                          }
                        }
                        }
                        onBlur={() => setFieldTouched("date", true)}
                        isInvalid={
                          errors.date &&
                          touched.date}
                      />
                    </div>
                    <label className="ul-form__label ul-form--margin col-lg-3 col-form-label fw-bold">
                      Vehicle Number :
                    </label>
                    <div className="col-lg-2">
                      <FormControl value={values.vehicle_number} name="vehicle_number" type="text" placeholder="Vehicle Number"
                        onChange={handleChange} onBlur={handleBlur} isInvalid={errors.vehicle_number && touched.vehicle_number} />
                    </div>
                  </div>

                  <div className="form-group row">
                    <label className="ul-form__label ul-form--margin col-lg-3 col-form-label fw-bold">
                      Genes :
                    </label>
                    <div className="col-lg-2">
                      <Form.Select
                        name="genes"
                        value={values.genes}
                        onChange={(value) => {
                          setFieldValue('genes', value.target.value)
                        }}
                        onBlur={() => setFieldTouched('genes', true)}
                        tabIndex={5}
                        isInvalid={
                          errors.genes && touched.genes
                        }
                      >
                        <option value="0" key="">Select ...</option>
                        {GenesGroup.map((item, index) => (
                          <option value={item.value} key={item.value}>{item.label}</option>
                        ))}
                      </Form.Select>
                    </div>
                    <label className="ul-form__label ul-form--margin col-lg-3 col-form-label fw-bold">
                      Quantity :
                    </label>
                    <div className="col-lg-2">
                      <div className="input-group">
                        <FormControl value={values.quantity} name="quantity" type="text" inputMode='numeric'
                          placeholder="QTY" onChange={handleChange} onBlur={handleBlur} isInvalid={errors.quantity &&
                            touched.quantity} tabIndex={6} />

                        <div className="input-group-append">
                          <span className="input-group-text" id="basic-addon2" style={{ borderTopLeftRadius: "0rem", borderBottomLeftRadius: "0rem" }}>
                            Quintal
                          </span>
                        </div>
                      </div>
                    </div>

                  </div>
                  <div className="form-group row">
                    <label className="ul-form__label ul-form--margin col-lg-3 col-form-label fw-bold">
                      Frieght :
                    </label>
                    <div className="col-lg-2">
                      <FormControl value={values.remarks} name="remarks" type="text" placeholder="Any Comment"
                        onChange={handleChange} onBlur={handleBlur} isInvalid={errors.remarks && touched.remarks} />

                    </div>
                    <label className="ul-form__label ul-form--margin col-lg-3 col-form-label fw-bold">
                      Any Advance :
                    </label>
                    <div className="col-lg-2">
                      <FormControl value={values.remarks} name="remarks" type="text" placeholder="Any Comment"
                        onChange={handleChange} onBlur={handleBlur} isInvalid={errors.remarks && touched.remarks} />

                    </div>
                  </div>
                  
                  <div className="form-group row">
                    <label className="ul-form__label ul-form--margin col-lg-3 col-form-label fw-bold">
                      Loading From :-
                    </label>
                    <div className="col-lg-2">
                      <Select
                        isClearable
                        isDisabled={isLoadingParty}
                        isLoading={isLoadingParty}
                        onChange={(value) => { setFieldValue("loading_from", value ? value.val : null); this.setState({ details: { ...this.state.details, loading_from: value ? value.val : null } }); this.setState({ PartyValue: value }) }}
                        options={PartyOptions}
                        value={PartyValue}
                        tabIndex={3}
                        onBlur={() => setFieldTouched("loading_from", true)}
                        isInvalid={
                          errors.party &&
                          touched.party}
                      />

                    </div>
                  </div>
                  
                  <div className="custom-separator"></div>
                  <FieldArray name="pendingPurchaseOrder">

                    {isMobile() ?
                      <>
                        {values.pendingPurchaseOrder?.map((item, index) => {
                          return (
                            <div className="mt-2 ms-2 me-2 mb-2" key={item.id}>
                              <Card
                                elevation={0}

                                style={{ backgroundColor: "#ADD8E6" }}
                                key={item.id}

                              >
                                <div className="row mt-2 ms-2 me-2 mb-2">
                                  <table className="table table-borderless" style={{ marginBottom: "0rem" }}>
                                    <tbody>
                                      <tr>
                                        <th>Date</th>
                                        <td>{moment(new Date(item.date)).format("DD-MMM-YYYY")}</td>
                                      </tr>
                                      <tr>
                                        <th>Total Quantity</th>
                                        <td className="text-capitalize">{item.quantity ? item.quantity : 0} {item.unit}</td>
                                      </tr>
                                      <tr>
                                        <th>Rate</th>
                                        <td className="text-capitalize">{item.rate ? item.rate : 0} Per QTL</td>
                                      </tr>
                                      <tr>
                                        <th>Pending Quanity</th>
                                        <td className="text-capitalize">{item.pending ? item.pending : 0} {item.unit}</td>
                                      </tr>
                                      <tr>
                                        <th>Quanity Loaded</th>
                                        <td className="text-capitalize"><div className="input-group">
                                          <input type="text" className="form-control" inputMode='numeric' style={{ borderTopRightRadius: "0.25rem", borderBottomRightRadius: "0.25rem", borderColor: "#003473" }}>
                                          </input>
                                          <div className="input-group-append">
                                            <span className="input-group-text" id="basic-addon2" style={{ backgroundColor: "#ADD8E6", borderRadius: "0", borderColor: "#ADD8E6" }}>
                                              {item.unit}
                                            </span>
                                          </div>
                                        </div></td>
                                      </tr>
                                    </tbody>
                                  </table>
                                </div>

                              </Card>
                              <div style={{ borderBottom: "1px dashed #003473", margin: "15px 0" }}></div>
                            </div>
                          )
                        })}
                      </> :

                      <div className="table-responsive">
                        <table className="table table-bordered" style={{ width: "100%", tableLayout: "fixed" }}>
                          {values.pendingPurchaseOrder?.length === 0
                            ? <></> :
                            <thead className="bg-gray-300">
                              <tr>
                                <th>Date</th>
                                <th scope="col">Item Name</th>
                                <th scope="col">Total Quantity</th>
                                <th scope="col">Rate</th>
                                <th scope="col">Pending Quantity</th>
                                <th scope="col">Quantity Loaded</th>
                              </tr>
                            </thead>}
                          <tbody>
                            {values.pendingPurchaseOrder?.filter(order => { if (values.genes) { return order.genes === values.genes } else return true }).map((item, index) => {
                              return (
                                <tr key={index}>
                                  <td>{moment(new Date(item.date)).format("DD-MMMM-YYYY")}</td>
                                  <td className="text-uppercase">{item.genes}</td>
                                  <td className="text-capitalize">{item.quantity} {item.unit}</td>
                                  <td className="text-capitalize">{item.rate}</td>
                                  <td className="text-capitalize">{item.pending ? item.pending - values.pendingPurchaseOrder[index].quantity_loaded : 0} {item.unit}</td>
                                  <td className="text-capitalize">
                                    <div className="input-group">
                                      <FormControl data-key={item.id} value={values.pendingPurchaseOrder[index].quantity_loaded} name={`pendingPurchaseOrder[${index}].quantity_loaded`} type="text" placeholder="Any Comment"
                                        onChange={handleChange} onBlur={handleBlur} isInvalid={errors.pendingPurchaseOrder && touched.pendingPurchaseOrder && errors.pendingPurchaseOrder[index] && touched.pendingPurchaseOrder[index] && errors.pendingPurchaseOrder[index].quantity_loaded && touched.pendingPurchaseOrder[index].quantity_loaded} />
                                      <div className="input-group-append" >
                                        <span className="input-group-text" style={{ borderTopLeftRadius: "0rem", borderBottomLeftRadius: "0rem" }}>
                                          {item.unit}
                                        </span>
                                      </div>
                                    </div>
                                  </td>
                                </tr>
                              );
                            })}
                          </tbody>
                        </table>
                      </div>}
                  </FieldArray>
                </Form>
              </Fragment>
            );
          }}
        </Formik>
      </div>
    );
  }
}
const loadingSchema = yup.object().shape({
  loading_from: yup.string().required(),
  date: yup.string().required(),
  genes: yup.string().required(),
  quantity: yup.string().required(),
  vehicle_number: yup.string().required(),
  remarks: yup.string().optional(),
  pendingPurchaseOrder: yup.array().of(yup.object().shape({
    broker: yup.string().optional().nullable(),
    broker_name: yup.string().optional().nullable(),
    condition: yup.string().optional().nullable(),
    date: yup.string().optional(),
    genes: yup.string().optional(),
    id: yup.string().optional(),
    party: yup.string().optional(),
    party_name: yup.string().optional(),
    pending: yup.number().optional(),
    purchase_type: yup.string().optional(),
    quantity: yup.number().optional(),
    rate: yup.number().optional(),
    remarks: yup.string().optional().nullable(),
    unit: yup.string().optional(),
    quantity_loaded: yup.string().optional().nullable().test("max", "Total should not exceed 100 %", function (value) {
      const { pending } = this.parent;
      if (value === undefined) {
        return true
      } else {
        return value <= pending;
      }
    })
  })),
});

export default withRouter(LoadingEditor);